<template>
  <div id="loginPage" >
    <el-row :gutter="0">
      <el-col :span="10" :offset="7">
        <div class="loginFormContent" >
          <div style="text-align: center;">
            <h3></h3>
            <div>
              <el-tag type="success"></el-tag>
              <el-tag type="warning"></el-tag>
              <el-tag></el-tag>
              <el-tag type="success"></el-tag>
              <el-tag type="warning"></el-tag>
            </div>
          </div>
          <div>
            <el-form ref="ruleForm" :model="ruleForm" :rules="rules" status-icon label-position="top" label-width="100px" class="demo-ruleForm" @submit.native.prevent >
              <el-form-item label="" prop="userName">
                <el-input v-model="ruleForm.userName" placeholder="" autocomplete="off" ></el-input>
              </el-form-item>
              <el-form-item label="" prop="pass">
                <el-input v-model="ruleForm.pass" type="password" placeholder="" autocomplete="off" show-password ></el-input>
              </el-form-item>
              <el-form-item label="" prop="checkPass">
                <el-input v-model="ruleForm.checkPass" type="password" autocomplete="off" placeholder="" show-password ></el-input>
              </el-form-item>
              <el-form-item label="" prop="birth">
                <el-date-picker v-model="ruleForm.birth" type="date" placeholder=""></el-date-picker>
              </el-form-item>
              <el-form-item label="" prop="sex">
                <el-input v-model="ruleForm.sex" type="string" placeholder="" autocomplete="off" ></el-input>
              </el-form-item>
              <el-form-item label="" prop="email">
                <el-input v-model="ruleForm.email" type="email" placeholder="" autocomplete="off" ></el-input>
              </el-form-item>
              <el-form-item label="" prop="phone">
                <el-input v-model="ruleForm.phone" type="string" placeholder="" autocomplete="off" ></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" native-type="submit" style="width: 100%;" @click="register('ruleForm')"></el-button>
              </el-form-item>
            </el-form>
          </div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import DwAuthorized from '../../utils/dw-authorized'
import {msgError} from '../../utils/dw-msg'
import {dwRegister} from '@/api/dw-register'

export default {
  name: 'Register',
  data () {
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
      } else if (value !== this.ruleForm.pass) {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    return {
      ruleForm: {
        userName: '',
        pass: '',
        checkPass: '',
        birth: '',
        sex: '',
        email: '',
        phone: ''
      },
      rules: {
        userName: [
          {required: true, message: '', trigger: 'blur'},
          {min: 3, max: 10, message: ' 3  10 ', trigger: 'blur'}
        ],
        pass: [
          {required: true, message: '', trigger: 'blur'},
          {min: 6, max: 18, message: ' 6  18 ', trigger: 'blur'}
        ],
        checkPass: [
          {required: true, validator: validatePass, trigger: 'blur'},
          {min: 6, max: 18, message: ' 6  18 ', trigger: 'blur'}
        ],
        sex: [
          {required: true, message: '', trigger: 'blur'},
          {type: 'enum', enum: ['', '']}
        ],
        birth: [
          {required: true, message: '', trigger: 'blur'},
          {type: 'date', format: 'yyyy-MM-dd', message: '', trigger: 'blur'}
        ],
        email: [
          {required: true, message: '', trigger: 'blur'},
          {type: 'email', message: '', trigger: ['blur', 'change']}
        ],
        phone: [
          {required: true, message: '', trigger: 'blur'},
          {min: 11, max: 11, message: '', trigger: 'blur'}
        ]
      }
    }
  },
  mounted () {
    this.pageH = window.height
  },
  methods: {
    register (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          dwRegister(this.ruleForm.userName, this.ruleForm.pass, this.ruleForm.birth, this.ruleForm.sex, this.ruleForm.email, this.ruleForm.phone).then((response) => {
            const resultData = response.data
            console.log('login-begin')
            console.log(resultData)
            if (resultData.status === 'ok') {
              DwAuthorized.setAuthority(resultData.currentAuthority)
              DwAuthorized.setUserName(this.ruleForm.userName)
              this.$router.push('/dw/survey/')
            } else {
              if (resultData.hasOwnProperty('httpResult') && resultData.httpResult != null && resultData.httpResult.hasOwnProperty('resultMsg')) {
                msgError(resultData.httpResult.resultMsg)
              } else {
                msgError('')
              }
            }
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    }
  }
}

</script>

<style scoped>
#loginPage {
    /*background-image: url("http://localhost:8181/diaowen/images/style-model/login_bg/1.jpg");*/
}

.loginFormContent {
  padding: 20px;
  margin-top: 50px;
  background-color: white;
  border: 1px solid gainsboro;
  border-radius: 3px;
}
</style>



